<template>
<v-container grid-list-xl fluid>
    <v-layout row wrap>
        <v-flex sm12>
            <h3>Clientes</h3>
        </v-flex>
        <v-flex xs12 sm12 md12>
            <v-card>
                <v-toolbar flat color="grey lighten-2">
                    <v-toolbar-title>
                        <v-icon>search</v-icon>
                    </v-toolbar-title>
                    <v-text-field :loading="loading" v-model="search" class="mx-3" flat hide-no-data hide-details label="Buscar" solo-inverted></v-text-field>
                </v-toolbar>
                <v-list subheader>
                    <v-list-tile v-for="item in filteredList" :key="item._id" @click="getUser(item._id)">
                        <v-list-tile-content>
                            <v-list-tile-title v-html="item.name"></v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list>
            </v-card>
        </v-flex>
    </v-layout>
</v-container>
</template>

<script>
import services from "../services/userService";

export default {
    data() {
        return {
            user: {},
            search: '',
            users: []
        }
    },
    methods: {
        listarUsuarios() {
            services.listarUsuarios().then(r => {
                console.log(r);

                if (r.status == 200) {
                    this.users = r.data;
                }
            });
        },
        getUser(data) {
            this.$router.push("/user/" + data);
        }
    },
    mounted() {
        this.user = this.user = JSON.parse(this.$cookie.get("user"));
        this.listarUsuarios();
    },
    computed: {
        filteredList() {
            return this.users.filter(post => {
                return post.name.toLowerCase().includes(this.search.toLowerCase())
            })
        }
    }

}
</script>
